import React from 'react'
import {View, Panel, PanelHeader, ScreenSpinner, List, Cell, Group} from '@vkontakte/vkui'

class Reports extends React.Component {
    constructor() {
        super()
        this.state = {
            activePanel: 'reports',
            reports: [],
            popout: <ScreenSpinner />
        }
    }

    componentDidMount() {
        fetch(sessionStorage.getItem('address') + '/api/v0/allreports', {
            method: 'GET',
            headers: {
                'tehnarenok-token': sessionStorage.getItem('token')
            }
        }).then(data => {
            return data.json()
        }).then(data => {
            this.setState({reports: data.reports, popout: null})
        })
    }

    render() {
        console.log(this.state)
        return (
            <View id={this.props.id} activePanel={this.state.activePanel} popout={this.state.popout}>
                <Panel id='reports'>
                    <PanelHeader>
                        Отчеты
                    </PanelHeader>
                    <Group>
                        <List>
                            {this.state.reports && this.state.reports.length > 0 && this.state.reports.map((value) => {
                                return (
                                    <Cell key={value._id} description={value.discription}>{value.name}</Cell>
                                )
                            })}
                        </List>
                    </Group>
                </Panel>
            </View>
        )
    }
}

export default Reports